* {
  list-style: none;
}

footer {
  background: #242c2e;
  font-family: 'Poppins', sans-serif;
  margin-top: 200px;
  font-family: poppins;
}

.fflex {
  display: flex;
  justify-content: space-around;
  padding-top: 80px;
  padding-bottom: 80px;
}

.firstaxis {
  color: #ebebeb;
}

.firstaxis h2 {
  font-size: 18px;
}

.firstaxis p {
  font-size: 15px;
  line-height: 2;
  color: #bbbab9;
}

.secondaxis {
  color: #ebebeb;
}

.secondaxis h2 {
  font-size: 18px;
}

.secondaxis p {
  font-size: 12px;
  line-height: 2;
  color: #bbbab9;
}

.fbg {
  background: #03031f;
  padding-top: 5px;
  padding-bottom: 80px;
  font-family: 'proxima nova';
}

.secondaxis img {
  margin-bottom: 20px;
}

.secondaxis li {
  margin-bottom: 20px;
}

.fflex {
  display: flex;
}

h2 {
  font-family: 'Millik';
  font-weight: 200;
  font-size: 25px;
  margin-bottom: 35px;
}

@media (max-width: 770px) {
  .fflex {
    display: block;
  }

  .firstaxis {
    margin-left: 40px;
  }
}