.nav_list .currentpage {
  font-weight: 800;
  color: #000038;
}

.nav_list ul li {
  display: inline;
  padding-right: 45px;
  font-family: 'Proxima Nova';
  font-size: 15px;
  letter-spacing: 0;
  font-weight: 500;
}

.form h1 {
  font-family: 'Millik';
  font-style: normal;
  font-weight: 200;
  font-size: 30px;
  line-height: 140.19%;
  color: #000038;
}

.auhero {
  width: 100%;
  padding-bottom: 50px;
  padding-top: 70px;
  font-family: 'Millik', sans-serif;
  color: #ebc608;
  font-size: 40px;
  font-weight: 50;
}

.auhero h1 {
  font-style: normal;
}

.tohelp {
  color: #000038;
}

.ourvalues h3 {
  font-family: 'millik', sans-serif;
  color: #000038;
  text-align: center;
}

.ourvalues p {
  text-align: center;
  align-items: center;
  font-size: 19px;
  font-family: 'Proxima Nova';
  font-style: normal;
  font-weight: 300;
  line-height: 141.19%;
}

.aucircle {
  background-color: white;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin: auto;
  margin-bottom: 35px;
}

.aucard {
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  text-align: center;
  padding: 30px 20px 50px 20px;
}

.aucard h3 {
  margin-bottom: 35px;
  color: #000038;
  font-weight: 400;
  font-style: normal;
  font-family: 'Millik';
  font-size: 21px;
}

.aucard p {
  font-family: 'Proxima Nova';
  font-style: normal;
  font-weight: 200;
  font-size: 16.3px;
  line-height: 1.7rem;
}

.secondgap {
  display: grid;
  grid-template-columns: repeat(auto-fit, 330px);
  gap: 60px;
  margin: auto;
  margin-top: 70px;
  justify-content: center;
}

.grid1 {
  background-color: #e7effa;
}

.grid2 {
  background-color: #f4f3ee;
}

.grid3 {
  background-color: #e7effa;
}

.aucircle h4 {
  text-align: center;
  margin: auto;
  padding-top: 5.2px;
  color: #000038;
  font-family: 'Millik';
}

.aucardgrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, 330px);
  gap: 60px;
  margin: auto;
  justify-content: center;
}

.ourvalues {
  margin-top: 50px;
}

.ourvalues p {
  margin-top: 40px;
  margin-bottom: 70px;
}

@media (max-width: 770px) {
  .auhero {
    font-size: 28px;
    padding-top: 50px;
    font-weight: 200;
  }

  .aucardgrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, 275px);
    gap: 60px;
    margin: auto;
    justify-content: center;
  }

  .secondgap {
    grid-template-columns: repeat(auto-fit, 275px);
  }
}