p{
   font-family: 'proxima nova'
}

h1{
   font-family: 'millik'
}

.nav_flex {
   display: flex;
   justify-content: space-between;
   margin: auto;
   margin-top: 40px;
}

.nav_flex2 {
   display: flex;
   justify-content: space-between;
   margin: auto;
   margin-top: 40px;
}

.nav_flex2 {
   display: none;
}

.nav_list {
   list-style: none;
   display: inline;
}

.nav_list .currentpage {
   font-weight: 800;
   color: #000038;
}

.nav_list ul li a {
   display: inline;
   padding-right: 45px;
   font-family: 'Proxima Nova';
   font-size: 15px;
   letter-spacing: 0;
   font-weight: 500;
   text-decoration: none;
}

.container {
   width: 85%;
   margin: auto;
}

.Hero {
   font-size: 41px;
   padding-top: 10px;
   font-family: 'Millik', sans-serif;
}

.Heroyellowtext {
   color: #ebc608;
}

.a1 {
   opacity: 0;
}

.a2 {
   opacity: 0;
}

.a3 {
   opacity: 0;
}

.a4 {
   opacity: 0;
}

.a5 {
   opacity: 0;
}

.Herobluetext {
   color: #000038;
}

.herotext {
   font-family: 'Proxima Nova';
   font-weight: 700;
   font-style: normal;
   font-size: 25px;
   line-height: 35px;
   margin-top: -40px;
}

.btn {
   background-color: #000038;
   color: white;
   font-size: 20px;
   font-family:'proxima nova';
   padding: 10px 40px;
   border-radius: 10px;
   margin: 10px 0px;
   cursor: pointer;
   margin-top: -5px;
}

.rotate {
   -moz-transition: all 2s linear;
   -webkit-transition: all 2s linear;
   transition: all 2s linear;
}

.rotate.down {
   -ms-transform: rotate(180deg);
   -moz-transform: rotate(180deg);
   -webkit-transform: rotate(180deg);
   transform: rotate(180deg);
}

.laptop p {
   background: #c4c4c4;
   width: 30px;
   height: 30px;
   padding: 8px;
   border-radius: 50%;
}

.mainicon {
   width: 2px;
}

.cardflex {
   display: grid;
   grid-template-columns: repeat(auto-fit, 300px);
   gap: 70px;
   margin: auto;
   margin-top: 70px;
   justify-content: center;
}

.card {
   background-color: #fffefe;
   box-shadow: 15px 15px 30px rgba(0, 0, 0, 0.1);
   padding: 15px 40px 15px 40px;
   margin: top;
   align-items: center;
}

.cardinnerflex {
   display: flex;
   align-items: center;
   margin: left;
}

.laptop {
   margin-right: 30px;
}

.carddetails p {
   font-family: 'Proxima Nova';
   font-style: normal;
   font-weight: 500;
   font-size: 13px;
   line-height: 2rem;
   margin-top: -3px;
}

.title h3 {
   font-family: 'Proxima Nova';
   font-style: normal;
   font-weight: 700;
   font-size: 17px;
   line-height: 140.19%;
}

.sdimage img {
   width: 70%;
   margin-top: 180px;
   float: right;
}

.clr {
   clear: both;
}

.sdtitle h2 {
   font-family: 'Millik';
   font-weight: 100;
   font-size: 40px;
   margin-top: 60px;
   line-height: 140.19%;
   /* color:red; */
}

.software {
   color: #1e94d1;
}

.sdtitle p {
   font-family: 'Proxima Nova';
   font-style: normal;
   font-weight: 400;
   font-size: 21px;
   margin-top: -10px;
   line-height: 1.6rem;
}

.sdtitle hr {
   height: 3px;
   width: 50%;
   background: #ebc608;
   float: right;
   margin-top: 110px;
}

.world {
   margin-top: 180px;
}

.worldflex {
   display: flex;
   position: relative;
}

.worldimage img {
   width: 65%;
}

.arrow img {
   position: absolute;
   flex-basis: 50%;
   right: 100px;
   top: 400px;
}

.world h2 {
   font-family: 'Millik';
   font-weight: 100;
   font-size: 40px;
   margin-top: 60px;
   line-height: 140.19%;
}

.cross {
   color: #ebc608;
}

.world p {
   font-family: 'Proxima Nova';
   font-style: normal;
   font-weight: 400;
   font-size: 21px;
   margin-top: -10px;
   line-height: 1.7rem;
}

.world hr {
   height: 3px;
   width: 50%;
   background: #1e94d1;
   float: left;
   margin-top: 110px;
}

.firstaiimage {
   margin-top: 180px;
