.parentbg {
  background: #F2F3EE;
  padding-top: 50px;
  margin-top: 20px;
}

.cuflex {
  display: flex;
  justify-content: space-between;
  margin: auto;
  align-items: center;
  width: 84%;
  margin-top: 80px;
}

.cutext {
  flex-basis: 50%;
}

.cutext p {
  font-size: 22px;
  font-family: 'proxima nova';
  line-height: 1.7em;
  margin-top: -10px;
}

.cuimage {
  flex-basis: 35%;
}

.cutext h2 {
  font-family: 'millik';
  font-style: normal;
  font-weight: 400;
  font-size: 65px;
  line-height: 140.3%;
  color: #222222;
}

.slantline {
  margin-top: -210px;
}

.Form {
  background-color: white;
  padding: 100px;
  margin-top: 150px;
}

.Form h1 {
  font-family: 'Millik';
  color: #000038;
  font-size: 27px;
  margin-bottom: 35px;
}

.Form p {
  font-family: 'Proxima Nova';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 170.2%;
  margin-bottom: 60px;
}

.Form input {
  font-family: 'Proxima nova';
  font-size: 18px;
  padding: 15px 0px 15px 30px;
  width: 90%;
  padding: #f1f1fd;
  color: #494949;
  border: none;
}

input[type='text'] {
  background-color: #f1f1fd;
  margin-bottom: 30px;
}

.inputflex {
  display: flex;
  justify-content: space-between;
}

@keyframes pulse {
  0% {
    transform: scale(0.84);
    opacity: 0.6s;
  }

  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.connectflex {
  padding-top: 130px;
  display: flex;
  justify-content: space-between;
  margin: auto;
  align-items: center;
}

.connect {
  flex-basis: 50%;
}

.connect p {
  font-size: 20px;
  margin-bottom: 10px;
  font-family: 'proxima nova';
}

.connect h2 {
  font-family: 'Millik';
  color: #000038;
}

.socialflexbg {
  padding: 5px;
  border-radius: 150px;
  border: 5px solid white;
  height: 260px;
  width: 260px;
  z-index: 1;
  animation: pulse 3000ms infinite;
}

.socialflex {
  flex-basis: 40%;
  height: 90px;
  width: 90px;
  border-radius: 150px;
  border: 45px solid white;
  text-align: center;
  margin: auto;
  padding-top: 5.2px;
  margin-top: 40px;
}

.socialflex1 {
  display: flex;
  justify-content: space-between;
  gap: 2rem;
  margin-bottom: 40px;
}

.socialflex2 {
  display: flex;
  justify-content: space-between;
  gap: 2rem;
}

@media (max-width: 770px) {
  .cutext h2 {
    font-Size: 40px;
    font-family: 'millik';
    font-style: normal;
    font-weight: 400;
    line-height: 50.09px;
    color: #222222;
  }

  .cuflex {
    display: block;
    margin-top: 10px;
  }

  .cutext p {
    font-size: 22px;
    font-family: 'proxima nova';
    line-height: 1.7em;
    font-weight: 300;
    font-size: 17px;
    margin-top: 20px;
  }

  .slantline {
    margin-top: -40px;
  }

  .cuimage img {
    margin-top: 60px;
  }

  .Form {
    background-color: white;
    padding: 30px;
    margin-top: 100px;
  }

  .connectflex {
    padding-top: 130px;
    display: block;
  }

  .connect h2 {
    font-size: 22px;
  }

  .connect p {
    font-weight: 300;
    margin-top: 20px;
    margin-bottom: 30px;
  }

  .socialflexbg {
    padding: 5px;
    border-radius: 150px;
    border: 4px solid white;
    height: 260px;
    width: 260px;
    z-index: 1;
    margin: auto;
    text-align: center;
    animation: pulse 3000ms infinite;
  }
}