.pbg {
   margin-top: 20px;
}

.pflex {
   display: flex;
   justify-content: space-between;
   margin: auto;
   align-items: center;
   width: 84%;
   margin-top: 80px;
}

.ptext {
   flex-basis: 50%;
}

.ptext p {
   font-size: 22px;
   font-family: 'proxima nova';
   line-height: 1.7em;
   margin-top: -10px;
}

.pimage {
   flex-basis: 35%;
}

.ptext h2 {
   font-family: 'millik';
   font-style: normal;
   font-weight: 400;
   font-size: 50px;
   line-height: 1.3em;
   color: #222222;
}

@media (max-width: 770px) {
   .ptext h2 {
      font-Size: 40px;
      font-family: 'millik';
      font-style: normal;
      font-weight: 400;
      line-height: 50.09px;
      color: #222222;
   }

   .pflex {
      display: block;
      margin-top: 10px;
   }

   .text p {
      font-size: 22px;
      font-family: 'proxima nova';
      line-height: 1.7em;
      font-weight: 300;
      font-size: 17px;
      margin-top: 20px;
   }

   .pimage img {
      margin-top: 60px;
   }
}